import React, {Component} from 'react';
import {connect} from 'react-redux';
import {setCompleted} from '../actions';
import {completeGoalRef} from '../firebase.js';
import CompleteGoalItem from './CompleteGoalItem.js'


class CompleteGoalList extends Component {

	componentDidMount() {
		completeGoalRef.on('value', snap => {
			let completeGoals = [];
			snap.forEach(completeGoal => {
				// console.log(completeGoal);
				const {email, title} = completeGoal.val();
				const serverKey = completeGoal.key;
				completeGoals.push({email, title, serverKey});
			});
			console.log('complete goals', completeGoals);
			this.props.setCompleted(completeGoals);
		});
	}

	clearCompleted() {
		completeGoalRef.set([]);
	}

	render() {
		console.log('this.props.completeGoals', this.props.completeGoals);
		return(
			<div>
				{
					this.props.completeGoals.map((completeGoal, index) => {
						// const {title, email} = completeGoal;
						// return(
						// 	<div style={{margin: '5px'}} key={index} >
						// 		<strong>{title}</strong> completed by <em>{email}</em>
						// 		<button
						// 			style={{marginLeft: '5px'}}
						// 			className="btn btn-sm btn-primary"
						// 			onClick={() => this.clearGoal()}
						// 		>
						// 			Clear Goal
						// 		</button>
						// 	</div>
						// );

						return <CompleteGoalItem key={index} completeGoal={completeGoal} />;
					})
				}
				<button
					className="btn btn-primary"
					onClick={() => this.clearCompleted()}
				>
					Clear All
				</button>
			</div>
		);
	}
}

function mapStateToProps(state) {
	const {completeGoals} = state;
	return {
		completeGoals: completeGoals
	};
}

export default connect(mapStateToProps, {setCompleted}) (CompleteGoalList);



