import React, {Component} from 'react';
import {completeGoalRef} from '../firebase.js';


class CompleteGoalItem extends Component {

	clearGoal() {
		// Remove this goal from complete goals reference
		const {serverKey} = this.props.completeGoal;
		completeGoalRef.child(serverKey).remove();
	}

	render() {
		// console.log('this.props.completeGoal', this.props.completeGoal);
		const {email, title} = this.props.completeGoal;
		return (
			<div style={{margin: '5px'}} >
				<strong>{title}</strong>
				<span style={{marginRight: '5px'}} > submitted by <em>{email}</em></span>
				<button
					className="btn btn-sm btn-primary"
					onClick={() => this.clearGoal()}
				>
					Clear Goal
				</button>
			</div>
		);
	}
}

export default CompleteGoalItem;



