import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { createStore } from 'redux';
import { BrowserRouter, Route } from 'react-router-dom';
import { Router } from 'react-router-dom'; // ni ovo verovatno nece trebati
import { firebaseApp } from './firebase.js';
import { logUser } from './actions';
import reducer from './reducers';


// Ovo ti nece trebati kao ni varijabla history verovatno
import createHistory from 'history/createBrowserHistory';



import App from './components/App.js';
import SignIn from './components/SignIn.js';
import SignUp from './components/SignUp.js';
import ResetPassword from './components/ResetPassword.js';

import registerServiceWorker from './registerServiceWorker';

const store = createStore(reducer);

const history = createHistory();
firebaseApp.auth().onAuthStateChanged(user => {
	if (user) {
		//console.log('user has signed in or up', user);
		const {email} = user;
		store.dispatch(logUser(email));
		history.push('/app');
	} else {
		//console.log('the user has signed out or still needs to sign in');
		history.replace('/signin');
	}
});

ReactDOM.render(
	<Provider store={store} >
		<Router history={history} path="/">
			<div>
				<Route exact path="/app" component={App}/>
				<Route path="/signin" component={SignIn}/>
				<Route path="/signup" component={SignUp}/>
				<Route path="/resetpassword" component={ResetPassword}/>
			</div>
		</Router>
	</Provider>, document.getElementById('root')
);
registerServiceWorker();



